.layout {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

header {
    height: 50px;
    background-color: lavender;
    display: flex;
    justify-content: center;
}

.wrapper {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer {
    height: 50px;
    background-color: lavender;
    display: flex;
    justify-content: center;
    align-items: center;
}

.userInfo {
    display: flex;
    align-items: center;
}

.avatar {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    margin-left: 10px;
}

@media screen and (max-width: 406px) {
    header {
        height: 80px;
    }
}
